<template>
    <div class="board">

        <!-- board title -->
        <h1 @click="filterData('language', 'JavaScript')">
          {{ user }}
        </h1>

        <div>

          <!-- filter cards -->
          <select v-model="language" @change="filterData('language', language)">
            <option v-for="language in languages">{{ language }}</option>
          </select>

          <!-- order by -->
          <span>Order by:</span>
          <span class="github-board__orderby" :class="{ top: order_by.key === 'stargazers_count' && order_by.direction === 1, bottom: order_by.key === 'stargazers_count' && order_by.direction === -1 }">
            <a href="javascript:" @click="orderBy('stargazers_count')">Stargazers</a>
          </span>
          <span class="github-board__orderby" :class="{ top: order_by.key === 'name' && order_by.direction === 1, bottom: order_by.key === 'name' && order_by.direction === -1 }">
            <a href="javascript:" @click="orderBy('name')">Repository</a>
          </span>
          <span class="github-board__orderby" :class="{ top: order_by.key === 'open_issues_count' && order_by.direction === 1, bottom: order_by.key === 'open_issues_count' && order_by.direction === -1 }">
            <a href="javascript:" @click="orderBy('open_issues_count')">Open issues</a>
          </span>
        </div>

        <!-- cards wrapper -->
        <div class="github-board__cards">

          <!-- cards loop -->
          <div is="card" v-for="repo in repos"
            :url="repo.html_url"
            :name="repo.name"
            :image="repo.owner.avatar_url"
            :description="repo.description"
            :language="repo.language"
            :stargazers-count="repo.stargazers_count"
            :open-issues-count="repo.open_issues_count"
            :created="repo.created_at"
            :pushed="repo.pushed_at"
            />
        </div>
    </div>
</template>

<script>
import Card from './Card'

export default {
  name: 'board',
  data () {
    return {
      user: 'wilfernandesjr',
      repos: [],
      _repos: [],
      language: 'Show all languages',
      languages: [],
      order_by: {
        key: '',
        direction: 0
      }
    }
  },
  mounted () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      this.$http.get('https://api.github.com/users/' + this.user + '/starred')
        .then(response => {
          this.repos = response.body
          this._repos = response.body

          // get all languages of fetched repositories
          let languages = this._repos.map(function (repo) {
            return repo.language ? repo.language : 'Without language'
          })
          languages = languages.filter(function (item, pos) {
            return item && languages.indexOf(item) === pos
          })
          this.languages = ['Show all languages'].concat(languages)
        })
    },
    filterData (key, value) {
      if (value === 'Show all languages') {
        // reset filter
        this.repos = this._repos
      } else if (key === 'language' && value === 'Without language') {
        // repos without language
        this.repos = this._repos.filter(function (repo) {
          return !repo.language
        })
      } else {
        // filter by key
        this.repos = this._repos.filter(function (repo) {
          return repo[key] === value
        })
      }
    },
    orderBy (key) {
      if (key === this.order_by.key) {
        // change direction
        this.order_by.direction *= -1
        this.repos = this.repos.reverse()

        // if (this.order_by.direction === -1) {
        //   // reset filter
        //   this.order_by.key = ''
        //   this.order_by.direction = 0
        //   this.repos = this._repos // fix this before
        // } else {
        // }
      } else {
        // order by key
        this.order_by.key = key
        this.order_by.direction = 1
        this.repos = this.repos.sort(function (curr, next) {
          curr = curr[key]
          next = next[key]

          if (isNaN(curr) && isNaN(next)) {
            return curr[0].toLowerCase() < next[0].toLowerCase() ? -1 : curr[0].toLowerCase() === next[0].toLowerCase() ? 0 : 1
          } else if (isNaN(curr)) {
            return 1
          } else if (isNaN(next)) {
            return -1
          }
          return curr - next
        })
      }
    }
  },
  components: {
    Card
  }
}
</script>

<style scoped>
.github-board__cards {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  width: 100%;
}
.github-board__orderby:after {
  border-style: solid;
	border-width: 0.24em 0.24em 0 0;
	content: '';
	display: none;
	height: 0.44em;
	width: 0.44em;
	left: 0.15em;
	position: relative;
	top: 6px;
	transform: rotate(-45deg);
	vertical-align: top;
}

.github-board__orderby.top:after {
  display: inline-block;
}

.github-board__orderby.bottom:after {
  display: inline-block;
	top: 3px;
	transform: rotate(135deg);
}

</style>



